<template>
	<view>
		<el-row :gutter="20">
			<el-col :span="6" v-for="(item,index) in iconList" :key="index">
				<el-card class="box-card" shadow="hover">
					<div style='height: 200rpx;padding-bottom: 25px;' slot="header" class="clearfix">
						<image style="width: 40px;height: 40px;margin-right: 20rpx;" :src="item.icon"></image>
						<span style="font-size: 48px;">{{item.num}}</span>
					</div>
					<div class="text item">
						{{item.desc}}
					</div>
				</el-card>
			</el-col>
		</el-row>
		<el-row :gutter="20">
			<el-col :span="12">
				<div class='mt-1-50 pd_2' style='background-color: #FFFFFF;'>
					<echarts :option="option" style="height: 420px;" ></echarts>
				</div>
			</el-col>
			<el-col :span="12">
				<div class='mt-1-50 pd_4' style='background-color: #FFFFFF;'>
					<echarts :option="option2" style="height: 400px;" ></echarts>
				</div>
			</el-col>
		</el-row>
		
	</view>
</template>

<script>
	import echarts from './echart/echart.vue';
	export default {
		data() {
			return {
				iconList:[
					{icon:'../../static/img/index/page1/2.png',num:'0',desc:'志愿者人数',bg_color:'bg-success'},
					{icon:'../../static/img/index/page1/1.png',num:'0',desc:'正在进行的任务',bg_color:'bg-primary'},
					{icon:'../../static/img/index/page1/3.png',num:'0',desc:'累计搜救成功人数',bg_color:'bg-info'},
					{icon:'../../static/img/index/page1/4.png',num:'0',desc:'累计搜救任务量',bg_color:'bg-warning'}
				],
				option : {
				    dataset: {
				        source: [
				            ['score', 'amount', 'product'],
				            [89.3, 58212, 'Matcha Latte'],
				            [57.1, 78254, 'Milk Tea'],
				            [74.4, 41032, 'Cheese Cocoa'],
				            [50.1, 12755, 'Cheese Brownie'],
				            [89.7, 20145, 'Matcha Cocoa'],
				            [68.1, 79146, 'Tea'],
				            [19.6, 91852, 'Orange Juice'],
				            [10.6, 101852, 'Lemon Juice'],
				            [32.7, 20112, 'Walnut Brownie']
				        ]
				    },
				    grid: {containLabel: true},
				    xAxis: {name: 'amount'},
				    yAxis: {type: 'category'},
				    visualMap: {
				        orient: 'horizontal',
				        left: 'center',
				        min: 10,
				        max: 100,
				        text: ['High Score', 'Low Score'],
				        // Map the score column to color
				        dimension: 0,
				        inRange: {
				            color: ['#D7DA8B', '#E15457']
				        }
				    },
				    series: [
				        {
				            type: 'bar',
				            encode: {
				                // Map the "amount" column to X axis.
				                x: 'amount',
				                // Map the "product" column to Y axis
				                y: 'product'
				            }
				        }
				    ]
				},
				option2 : {
				    title: {
				        text: '折线图堆叠'
				    },
				    tooltip: {
				        trigger: 'axis'
				    },
				    legend: {
				        data: ['邮件营销', '联盟广告', '视频广告', '直接访问', '搜索引擎']
				    },
				    grid: {
				        left: '3%',
				        right: '4%',
				        bottom: '3%',
				        containLabel: true
				    },
				    toolbox: {
				        feature: {
				            saveAsImage: {}
				        }
				    },
				    xAxis: {
				        type: 'category',
				        boundaryGap: false,
				        data: ['周一', '周二', '周三', '周四', '周五', '周六', '周日']
				    },
				    yAxis: {
				        type: 'value'
				    },
				    series: [
				        {
				            name: '邮件营销',
				            type: 'line',
				            stack: '总量',
				            data: [120, 132, 101, 134, 90, 230, 210]
				        },
				        {
				            name: '联盟广告',
				            type: 'line',
				            stack: '总量',
				            data: [220, 182, 191, 234, 290, 330, 310]
				        },
				        {
				            name: '视频广告',
				            type: 'line',
				            stack: '总量',
				            data: [150, 232, 201, 154, 190, 330, 410]
				        },
				        {
				            name: '直接访问',
				            type: 'line',
				            stack: '总量',
				            data: [320, 332, 301, 334, 390, 330, 320]
				        },
				        {
				            name: '搜索引擎',
				            type: 'line',
				            stack: '总量',
				            data: [820, 932, 901, 934, 1290, 1330, 1320]
				        }
				    ]
				},
				
			};
		},
		mounted() {
			this.getdata()
		},
		methods: {
			getdata() {
				new Promise((resolve, reject) => {
					uni.request({
						method: 'GET',
						url: this.$store.state.url + 'tasknumadmin/',
						success: (ret) => {
							resolve(ret.data)
						}
					})
				}).then((data) => {
					this.iconList[0].num = data.volunteerSum
					this.iconList[1].num = data.tasking
					this.iconList[2].num = data.tasked
					this.iconList[3].num = data.taskSum
				})
			    
			}
		},
		components: {
			echarts
		}
	}
</script>

